import {
  interpolateSkeleton,
  interpolateSkeletonVec,
  leaf,
  normalize2D,
} from '@plantarium/geometry';
// import { logger } from '@plantarium/helpers';
// const log = logger('nodes.leaf');

const defaultValue = [
  {
    x: 1,
    y: 1,
    pinned: true,
  },
  {
    x: 0.62,
    y: 0.84,
    pinned: false,
  },
  {
    x: 0.54,
    y: 0.63,
    pinned: false,
  },
  {
    x: 0.38,
    y: 0.41,
    pinned: true,
  },
  {
    x: 0.44,
    y: 0.23,
    pinned: false,
  },
  {
    x: 0.64,
    y: 0.12,
    pinned: false,
  },
  {
    x: 0.98,
    y: 0.02,
    pinned: true,
  },
];

const node: PlantNode = {
  title: 'Leaf',
  type: 'leaf',
  outputs: ['plant', 'leaf'],
  parameters: {
    input: {
      type: 'plant',
      external: true,
    },
    size: {
      type: ['number', 'parameter', 'curve', 'vec2'],
      inputType: 'slider',
      min: 0,
      max: 3,
      step: 0.05,
      value: 0.2,
    },
    shape: {
      type: 'leaf',
      external: true,
      defaultValue,
    },
    lowestLeaf: {
      type: 'number',
      inputType: 'slider',
      min: 0,
      max: 1,
      step: 0.01,
      value: 0.5,
    },
    curvature: {
      external: true,
      type: 'vec2',
      defaultValue: {
        x: 0.5,
        y: 0.2,
      },
    },
    amount: {
      type: 'number',
      min: 0,
      max: 20,
      value: 10,
    },
  },

  computeSkeleton(parameters, ctx) {
    const { input } = parameters;

    const { skeletons } = input.result;

    const instances = skeletons.map((skelly, i) => {
      const alpha = i / skeletons.length;

      const amount = ctx.handleParameter(parameters.amount, alpha);

      const offset = new Float32Array(amount * 3);
      const scale = new Float32Array(amount * 3);
      const rotation = new Float32Array(amount * 3);

      for (let i = 0; i < amount; i++) {
        const _alpha = i / (amount - 1);
        const size = ctx.handleParameter(parameters.size, 1 - _alpha);
        const lowestLeaf = ctx.handleParameter(parameters.lowestLeaf, _alpha);

        const a = lowestLeaf + (1 - lowestLeaf) * _alpha - 0.001;
        //const isLeft = i % 2 === 0;

        const [x, y, z] = interpolateSkeleton(skelly, a);
        const [_vx, , _vz] = interpolateSkeletonVec(skelly, a);

        const nv = normalize2D([_vx, _vz]);

        //Rotate Vector along stem by 90deg
        // const [vx, vz] = rotate2D(nv[0], nv[1], isLeft ? Math.PI : -Math.PI);

        // Find the angle of the vector
        const angleRadians = Math.atan2(-nv[0], nv[1]);

        offset[i * 3 + 0] = x;
        offset[i * 3 + 1] = y;
        offset[i * 3 + 2] = z;

        scale[i * 3 + 0] = size;
        scale[i * 3 + 1] = 1;
        scale[i * 3 + 2] = size;

        rotation[i * 3 + 0] = 0;
        rotation[i * 3 + 1] =
          angleRadians - (Math.PI / 2) * (i % 2 === 0 ? -1 : 1);
        rotation[i * 3 + 2] = 0;
      }

      return {
        offset,
        scale,
        rotation,
      };
    });

    return {
      allSkeletons: input.result.allSkeletons,
      skeletons,
      instances,
    };
  },

  computeGeometry(parameters, result, ctx) {
    const { shape, input, curvature } = parameters;
    const { instances } = result;

    const _curvature = ctx.handleParameter(curvature);

    const geometry = leaf(ctx.handleParameter(shape), {
      res: ctx.getSetting('leafRes', 2),
      xCurvature: _curvature.x,
      yCurvature: _curvature.y,
    });

    return {
      geometry: input.result.geometry,
      // Here we add the resulting geometry to the instances
      instances: instances.map((i) => ({ ...i, ...geometry })),
    };
  },
};

export default node;
